<template>
  <div class="list-group-item">
    <div class="main-content">
      <div class="main-left">
        <router-link :to="{ name: 'user-tweets', params: { id: follow.id } }">
          <img class="avatar" :src="follow.avatar | emptyImage" alt="avatar" />
        </router-link>
      </div>
      <div class="main-right">
        <p class="follow-name">{{ follow.name }}</p>
        <p class="text-muted">{{ follow.account }}</p>
        <p>{{ follow.introduction }}</p>
      </div>
    </div>
    <div class="side-content">
      <button
        v-if="isProcessing"
        type="button"
        class="btn btn-sm btn-primary follow-button"
        disabled
      >
        處理中
      </button>
      <template v-else>
        <button
          v-if="follow.isFollowed && (currentUserId !== follow.followId)"
          @click.prevent.stop="deleteFollow(follow.followId)"
          type="button"
          class="btn btn-sm btn-primary follow-button"
        >
          正在跟隨
        </button>
        <button
          v-if="(!follow.isFollowed) && (currentUserId !== follow.followId)"
          @click.prevent.stop="addFollow(follow.followId)"
          type="button"
          class="btn btn-sm btn-outline-primary follow-button"
        >
          跟隨
        </button>
      </template>
    </div>
  </div>
</template>

<script>
import followAPI from './../apis/follow'
import { Toast } from './../utils/helpers'
import { emptyImageFilter } from './../utils/mixins'
import { mapState } from 'vuex'
import { socket } from './../main'

export default {
  name: "UserCard",
  mixins: [emptyImageFilter],
  props: {
    initialFollow: {
      type: Object,
      required: true,
    },
    currentUserId: {
      type: Number,
      default: -1
    }
  },
  data() {
    return {
      follow: this.initialFollow,
      isProcessing: false
    }
  },
  computed: {
    ...mapState(['currentUser', 'recommendation'])
  },
  methods: {
    // socket 傳送追蹤通知
    socketFollow(followId) {
      const payLoad = {
        type: 'follow',
        userId: this.currentUser.id,
        followId
      }
      socket.emit('sendNotification', payLoad)
    },
    // 追蹤使用者
    async addFollow(followId) {
      try {
        const payLoad = {id: followId}
        this.isProcessing = true
        const { data } = await followAPI.addFollow({ payLoad })
        this.isProcessing = false
        if (data.status !== 'success') {
          throw new Error(data.message)
        }
        this.follow.isFollowed = true
        // 傳送通知
        this.socketFollow(followId)
        // 修改 vuex 資料
        this.$store.commit('followRecommendation', parseInt(followId))
      } catch(error) {
        Toast.fire({
          icon: 'error',
          title: '無法追蹤使用者，請稍後再試'
        })
        this.isProcessing = false
        console.log(error)
      }
    },
    // 取消追蹤使用者
    async deleteFollow(followId) {
      try {
        this.isProcessing = true
        const { data } = await followAPI.removeFollow({ followId })
        this.isProcessing = false
        if (data.status !== 'success') {
          throw new Error(data.message)
        }
        this.follow.isFollowed = false
        this.$emit('after-delete-follow', followId)
        // 修改 vuex 資料
        this.$store.commit('unfollowRecommendation', parseInt(followId))
      } catch(error) {
        Toast.fire({
          icon: 'error',
          title: '無法退追使用者，請稍後再試'
        })
        this.isProcessing = false
        console.log(error)
      }
    },
  },
  watch: {
    initialFollow() {
      this.follow = this.initialFollow
    },
    recommendation(data) {
      data.forEach(user => {
        if (user.id === parseInt(this.follow.followId)) {
          this.follow = {
            ...this.follow,
            isFollowed: user.isFollowed
          }
        }
      })
    }
  }
}
</script>

